  --sidebar-version-link-hover-background-color: #fff;
  --sidebar-version-link-border-color: #fff;
  --sidebar-version-link-border-hover-color: #000;
  --theme-switcher-color: #fff;
  --theme-switcher-indicator--dark-display: none;
  --theme-switcher-indicator--light-display: block;
  --version-note-link-color: #fff;
  --version-note-link-border-color: #54ffb9;
  --toc-link-hover-color: #54ffb9;
  --code-color: #54ffb9;
