/**
 * prism.js theme for Timber
 * @author Lukas Gächter
 */
/*
 * Added few hardcoded colors that I picked
 * from Firefox Dev Tool when checking color contract ratio,
 * since these colors only apply to code block for both light & dark themes,
 * I didn't extract these colors as global variables.
 */
code[class*=language-],
pre[class*=language-] {
  color: #fff;
  background: none;
  font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.8;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre[class*=language-]::-moz-selection, pre[class*=language-] ::-moz-selection,
code[class*=language-]::-moz-selection, code[class*=language-] ::-moz-selection {
  text-shadow: none;
  background: #b3d4fc;
}

pre[class*=language-]::selection, pre[class*=language-] ::selection,
code[class*=language-]::selection, code[class*=language-] ::selection {
  text-shadow: none;
  background: #b3d4fc;
}

@media print {
  code[class*=language-],
  pre[class*=language-] {
    text-shadow: none;
  }
}
/* Code blocks */
pre[class*=language-] {
  margin-left: 0;
  margin-right: 0;
  padding: 1em;
  overflow: auto;
}

:not(pre) > code[class*=language-],
pre[class*=language-] {
  background: #161616;
}

/* Inline code */
:not(pre) > code[class*=language-] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
}

code.language-html {
  color: #e6e6e6;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}

.token.punctuation {
  color: #cacaca;
}

.tag .ld .token.punctuation,
.tag .rd .token.punctuation {
  font-weight: 400;
}

.namespace {
  opacity: 0.7;
}

.token .tag {
  color: #e6e6e6;
}

.token .attr-name {
  color: #e6e6e6;
}

.token.property {
  color: #fff;
}

.tag .token.property {
  color: #54ffb9;
}

.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #FF7E81;
}

.token.selector,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #1dd288;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #FFB302;
}

.token.atrule,
.token.attr-value {
  color: #169c65;
}

.token.keyword {
  color: #fff;
  font-weight: 700;
}

.token.function,
.token.class-name {
  color: #54ffb9;
}

.token.regex,
.token.important {
  color: #e90;
}

.token.delimiter.important {
  color: #a2a7f2;
  font-weight: 700;
}

.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

